// src/pages/categories.js
import { useEffect, useState } from "react";
import Link from "next/link";

export default function CategoriesPage() {
  const [cats, setCats] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    async function load() {
      setLoading(true);
      try {
        const res = await fetch("/api/categories");
        const data = await res.json();
        setCats(data || []);
      } catch (e) {
        console.error("Failed to load categories", e);
      } finally {
        setLoading(false);
      }
    }
    load();
  }, []);

  return (
    <div style={{ fontFamily: "system-ui, sans-serif", padding: 20 }}>
      <header style={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
        <h1 style={{ margin: 0 }}>LiveMart — Categories</h1>
        <Link href="/cart">Cart</Link>
      </header>

      <p style={{ color: "#666" }}>Top-level categories (click to browse):</p>

      {loading ? <p>Loading…</p> : (
        <ul style={{ listStyle: "none", padding: 0 }}>
          {cats.map(c => (
            <li key={c._id} style={{
              background: "#fff",
              marginBottom: 10,
              padding: 12,
              borderRadius: 8,
              boxShadow: "0 1px 3px rgba(0,0,0,0.06)"
            }}>
              <div style={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
                <div>
                  <div style={{ fontWeight: 700 }}>
                    <Link href={`/products?categoryId=${c._id}`}>{c.name}</Link>
                  </div>
                  <div style={{ color: "#666", fontSize: 13 }}>{c.path}</div>
                </div>
                <div style={{ textAlign: "right" }}>
                  <Link href={`/products?categoryId=${c._id}`}>View items →</Link>
                </div>
              </div>
            </li>
          ))}
        </ul>
      )}

      <hr />
      <div style={{ color: "#666" }}>
        Tip: click a category to see products. Use the Cart link to open your cart and checkout.
      </div>
    </div>
  );
}
